/* eslint-disable @typescript-eslint/no-unused-vars */
"use client";

import { useState, useEffect } from "react";
import { FaEdit, FaTrash } from "react-icons/fa";
import SearchBar from "./SearchBar";
import EditJuryForm from "./EditJuryForm";
import DeleteJuryButton from "./DeleteJuryButton";

export default function JuryList() {
  const [jurys, setJurys] = useState<{ idJury: number; nom: string; valideDeliberation: boolean }[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [editingJury, setEditingJury] = useState<number | null>(null);
  const [confirmDeleteId, setConfirmDeleteId] = useState<number | null>(null);

  // Fetch jurys from the backend
  useEffect(() => {
    const fetchJurys = async () => {
      try {
        const response = await fetch("http://localhost:5000/api/jurys");
        const data = await response.json();
        setJurys(data);
      } catch (error) {
        console.error("Error fetching jurors:", error);
      }
    };

    fetchJurys();
  }, []); // Runs only once when the component mounts

  // Handle search input
  const handleSearch = (term: string) => {
    setSearchTerm(term);
  };

  // Filter jurys based on the search term
  const filteredJurys = jurys.filter((jury) =>
    jury.nom.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Handle edit of a jury
  const handleEdit = (juryId: number) => {
    setEditingJury(juryId);
  };

  // Save the edited jury details
  const handleSaveEdit = (updatedJury: { idJury: number; nom: string; valideDeliberation: boolean }) => {
    setJurys(jurys.map((jury) => (jury.idJury === updatedJury.idJury ? updatedJury : jury)));
    setEditingJury(null); // Close the edit form
  };

  // Handle delete of a jury with confirmation
  const handleDelete = (juryId: number) => {
    setConfirmDeleteId(juryId); // Trigger confirmation
  };

  const confirmDelete = () => {
    setJurys(jurys.filter((jury) => jury.idJury !== confirmDeleteId));
    setConfirmDeleteId(null); // Reset confirmation
  };

  const cancelDelete = () => {
    setConfirmDeleteId(null); // Cancel deletion
  };

  return (
    <div className="mt-8 p-6 bg-gray-50 rounded-lg shadow-xl">
      {/* Search Bar */}
      <SearchBar onSearch={handleSearch} />

      {/* Confirmation Modal for Deletion */}
      {confirmDeleteId !== null && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center z-10">
          <div className="bg-white p-6 rounded-xl shadow-lg max-w-sm w-full">
            <h3 className="text-xl font-semibold text-gray-800 mb-4">Confirmer la suppression</h3>
            <p className="text-gray-600 mb-4">Êtes-vous sûr de vouloir supprimer ce jury ?</p>
            <div className="flex justify-between gap-4">
              <button
                onClick={confirmDelete}
                className="bg-red-500 text-white px-5 py-2 rounded-lg hover:bg-red-600 transition duration-300"
              >
                Oui, supprimer
              </button>
              <button
                onClick={cancelDelete}
                className="bg-gray-400 text-white px-5 py-2 rounded-lg hover:bg-gray-500 transition duration-300"
              >
                Annuler
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Jury Table */}
      <div className="overflow-x-auto bg-white shadow-xl rounded-lg">
        <table className="min-w-full table-auto">
          <thead className="bg-gray-200 text-gray-600">
            <tr>
              <th className="px-6 py-3 text-left text-sm font-medium">Nom</th>
              <th className="px-6 py-3 text-left text-sm font-medium">Actions</th>
            </tr>
          </thead>
          <tbody>
            {filteredJurys.map((jury) => (
              <tr
                key={jury.idJury}
                className="border-t hover:bg-gray-50 transition duration-300"
              >
                <td className="px-6 py-4 text-sm text-gray-800">{jury.nom}</td>
                <td className="px-6 py-4 text-sm">
                  <div className="flex gap-4">
                    <button
                      onClick={() => handleEdit(jury.idJury)}
                      className="text-blue-500 hover:text-blue-700 transition duration-300"
                    >
                      <FaEdit />
                    </button>
                    <button
                      onClick={() => handleDelete(jury.idJury)}
                      className="text-red-500 hover:text-red-700 transition duration-300"
                    >
                      <FaTrash />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
